import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';

export class StickerGrid extends PureComponent {

    render() {
        return (
            this.props.mode === 'small' ?
                <div style={{ minWidth: "64px", maxWidth: "296px", display: "grid", gridTemplateColumns: "repeat(auto-fill, 64px)", gridGap: "10px" }}>{this.props.children}</div>
                :
                this.props.mode === 'big' &&
                <div style={{ minWidth: "96px", maxWidth: "320px", display: "grid", gridTemplateColumns: "repeat(auto-fill, 96px)", gridGap: "10px" }}>{this.props.children}</div>
        )
    }

}