import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import { Button, Cell} from "@vkontakte/vkui";
import {StickerImg} from "./StickerImg";

export class StickerButton extends PureComponent {

    render() {
        return (
            this.props.mode === 'cell' ?
                <Cell id={this.props.id} onClick={this.props.onClick} before={<StickerImg mode={'medium'} src={this.props.src}/>}
                      description={this.props.author} size={"l"}
                      bottomContent={<Button mode={this.props.hasOwnProperty('mode2') ? this.props.mode2 : "commerce"}>{this.props.hasOwnProperty('price') ? (this.props.price + ' руб.') : this.props.button}</Button>}>{this.props.title}</Cell>
                :
                this.props.mode === 'img' &&
                <StickerImg
                    mode={"big"}
                    src={this.props.src}
                    id={this.props.id}
                    onClick={this.props.onClick}
                />
        )
    }

}