import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import {Cell} from "@vkontakte/vkui";
import {StickerImg} from "./StickerImg";

export class StickerUploadButton extends PureComponent {

    render() {
        return (
            <div>
                <input type="file" ref={(ref) => { if(this.props.hasOwnProperty('src')) { this.upload_preview = ref; } else {  this.upload_stickers = ref } }} style={{display: "none"}} onChange={this.props.onChange}/>
                <Cell onClick={()=> { if(this.props.hasOwnProperty('src')) { this.upload_preview.click(); } else { this.upload_stickers.click(); } }} before={this.props.hasOwnProperty('src') && <StickerImg mode={'medium'} src={this.props.src}/>} multiline description={this.props.description} >{this.props.title}</Cell>
            </div>
        )
    }

}