import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';

export class StickerDescription extends PureComponent {

    render() {
        return (
            <div style={{ color: "#7f8285" }} dangerouslySetInnerHTML={{ __html: this.props.children.replace('\n','<br/>') }} />
        )
    }

}